.hunters-house {
  position: relative;
  display: block;
  min-height: 704px;
  padding: 108px 0 0 0px;

  @include mobile {
    padding-top: 40px;
  }

  // &::before {
  //   position: absolute;
  //   content: "";
  //   display: block;
  //   top: 0;
  //   left: 0;
  //   bottom: 0;
  //   right: 0;
  //   background-image: url("../img/house-black--lake.jpg");
  //   background-position: 0px -56px;
  //   background-repeat: no-repeat;
  //   background-size: cover;
  // background-attachment: fixed;
  // }

  &__container {
    position: relative;
    display: block;
    padding-bottom: 30px;
  }

  &__title {
    font-size: 50px;
    font-family: "Arkhip", sans-serif;
    text-transform: uppercase;
    color: var(--greeny-blue);
    padding: 0 0 56px 5px;
    letter-spacing: 0.97px;
    margin: 0;
    text-align: left;

    @include mobile {
      font-size: 34px;
    }
  }

  &__item {
    padding-left: 10px;
    position: relative;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    max-height: 421px;
    width: 75%;
    counter-reset: heading;

    @include small-tablet {
      max-height: 360px;
      margin-left: 30px;
    }

    @include mobile {
      max-height: 715px;
      justify-content: flex-start;
      margin-left: 91px;
    }
  }

  &::after {
    z-index: 100;
    position: absolute;
    content: "";
    right: 230px;
    bottom: 3px;
    height: 200px;
    width: 200px;
    background-image: url(../img/bird.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 80%;
    object-fit: cover;
  }

  &__items {
    position: relative;
    display: block;
    font-family: "Corbel", sans-serif;
    font-size: 35px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1;
    letter-spacing: normal;
    text-align: left;
    padding: 0 107px 35px 10px;
    margin-left: 43px;

    @include big-desktop {
      padding-right: 70px;
    }

    @include tablet {
      font-size: 24px;
    }

    @include small-tablet {
      padding-right: 45px;
      font-size: 19px;
      margin-left: 5px;
    }

    @include mobile {
      padding-right: 0;
      font-size: 21px;
      margin-left: 0px;
    }


    &::before {
      position: absolute;
      left: -50px;
      top: 0;
      font-family: "Corbel", sans-serif;
      font-size: 31.5px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: left;
      color: var(--greeny-blue);
      counter-increment: heading;
      content: "0"counter(heading) " ";

      @include mobile {
        top: -10px;
      }
    }

    &:nth-child(n + 10)::before {
      content: " "counter(heading) " ";
    }

    &__items:nth-child(6n + 1) {
      clear: both;
    }
  }

  &__svg {
    z-index: 10;
    position: absolute;
    top: 442px;
    right: -90px;
    min-width: 484px;
    min-height: 270px;

    @include small-tablet {
      top: 448px;
      right: 0px;
      min-width: 304px;
      min-height: 215px;
    }

    @include mobile {
      top: 680px;
      min-width: 103px;
      min-height: 115px;
    }
  }
}
