.parallax {
  position: relative;
  height: 100%;
}

.burger {

  display: none;
  outline: none;
  cursor: pointer;
  background: transparent;
  // background-color: #fbfbfb;
  z-index: 99;

  @include tablet {
    padding-top: vmin(30);
    width: vmin(25);
    height: vmin(20);
    // height: vmin(122);
    display: block;
  }

  @include mobile {
    width: vmin(25);
    height: vmin(20);
  }

  svg {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    fill: #000;
  }
}

#nav-icon3 {
  display: block;
  position: relative;
  margin: 0px auto;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  -moz-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: transform .5s ease-in-out;
  cursor: pointer;
}

#nav-icon3 span {
  display: block;
  position: absolute;
  height: 6px;
  width: 99%;
  background: var(--almost-black);
  border-radius: 9px;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .25s ease-in-out;
  -moz-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: transform .25s ease-in-out;

  @include mobile {
    height: 3px;
  }
}

#nav-icon3 span:nth-child(1) {
  top: -14px;
  background: var(--almost-black);

  @include mobile {
    top: -8px;
  }
}

#nav-icon3 span:nth-child(2),
#nav-icon3 span:nth-child(3) {
  top: -2px;
  background: var(--almost-black);

  @include mobile {
    top: 1px;
  }
}

#nav-icon3 span:nth-child(4) {
  top: 10px;
  background: var(--almost-black);
}

#nav-icon3.open span:nth-child(1) {
  top: 18px;
  width: 0%;
  left: 50%;
}

#nav-icon3.open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

#nav-icon3.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

#nav-icon3.open span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%;
}

.header {

  left: 0;
  top: 0;
  width: 100%;
  background-image: url(../img/fog.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  object-fit: cover;
  z-index: 99;

  &__nav {
    display: flex;
    -webkit-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    margin-bottom: 398px;

    @include big-desktop {
      margin-bottom: 0;
    }
  }

  &__list {
    position: absolute;
    align-items: center;
    display: flex;

    @include tablet {

      display: none;
    }
  }

  &__item {
    margin-top: vw(21);
    display: inline-block;
    transition: 1s;

    &:not(:last-child) {
      margin-right: vw(21);
    }
  }

  &__link {
    font-size: vw(29);
    color: var(--charcoal-grey);
    transition: color 0.5s;


    &:hover {
      transition: 0.3s;
      color: var(--very-green);
    }

    @include tablet {
      padding-left: 9px;
      font-size: vw(43);
    }

    @include small-tablet {
      font-size: vw(57);
    }
  }

  &__text {
    font-family: 'Corbel', sans-serif;
    align-self: center;
    top: 60%;
    left: 50%;
    font-size: vw(33);
    font-weight: normal;
    line-height: normal;
    text-align: center;
    margin: -12px 0 216px;


    @include tablet {
      font-size: vw(32);
      margin: -12px;
      top: 54%;
    }

    @include mobile {
      font-size: vw(55);
    }
  }

  &__parallax-fog {
    background: url('../img/fog_1.png') bottom / 100% no-repeat;
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 2;
    pointer-events: none;

    @include tablet {

      top: vw(-2100);
    }

    @include small-tablet {
      top: vw(-2900);
    }
  }

  &__headline {
    position: relative;
    width: 96%;
    display: flex;
    -webkit-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    color: var(--color-white);
    font-family: "Arkhip", sans-serif;
    font-weight: normal;
    font-size: vw(201);
    line-height: 202px;
    letter-spacing: -3.79px;
    text-align: center;
    margin-bottom: 9px;

    &::after {
      z-index: 100;
      position: absolute;
      content: "";
      right: 210px;
      top: 99px;
      height: 100px;
      width: 162px;
      background-image: url(../img/whitewave.png);
      background-position: center;
      background-repeat: no-repeat;
      -o-object-fit: cover;
      object-fit: cover;

      @include big-desktop {
        background-size: 79%;
        right: 284px;
        top: 90px;
      }

      @include tablet {
        background-size: 44%;
        right: 121px;
        top: 72px;
      }

      @include small-tablet {
        background-size: 35%;
        right: 74px;
        top: 66px;
      }

      @include mobile {
        background-size: 39%;
        right: -52px;
        top: 70px;
      }
    }


    @include tablet {
      font-size: vw(191);
    }

    @include mobile {
      font-size: vw(350);
    }
  }
}

.search-form {
  margin: 0px 10px 0 11px;
  left: 50%;
  top: 100%;
  // width: vw(1460);

  &__list {
    position: relative;
    top: vw(87);
    background-color: var(--color-white);
    padding: 60px 85px 57px 83px;
    display: flex;

    @include tablet {
      margin: 0 auto;
      width: 60%;
      flex-direction: column;
      padding: vw(20) vw(29) vw(17) vw(29);
      justify-content: center;
      flex-wrap: wrap;
    }

    @include mobile {
      width: 100%;
    }

  }

  &__item {
    width: 25%;
    height: auto;
    border: 2px solid var(--brownish-grey-43);
    text-align: center;
    vertical-align: middle;

    @include tablet {
      width: 100%;
    }

    &:first-child {
      border-top-left-radius: 11px;
      border-bottom-left-radius: 11px;

      @include tablet {
        border-bottom-left-radius: 0px;
        border-top-left-radius: 0px;
        border-top-right-radius: 0px;
      }
    }

    &:last-child {
      background-color: var(--greeny-blue);
      border-top-right-radius: 16px;
      border-bottom-right-radius: 18px;

      @include tablet {
        border-top-right-radius: 0px;
        border-bottom-left-radius: 0px;
        border-bottom-right-radius: 0px;
      }
    }

    &:not(:first-child) {
      border-left: 0px;

      @include tablet {
        border-left: 2px solid var(--brownish-grey-43);
        border-top: 0px;
      }
    }
  }

  &__link {
    padding: vw(26) vw(0);
    display: inline-block;
    text-transform: uppercase;
    color: var(--brownish-grey-43);
    font-size: vw(22.5);
    font-weight: bold;

    @include tablet {
      line-height: 17px;
      font-size: vw(17.5);
      padding: vw(18) 0;
    }

    @include small-tablet {
      line-height: 17px;
      font-size: vw(27.5);
      padding: vw(9) 0;
    }

    @include mobile {
      line-height: 23px;
      font-size: vw(38.5);
      padding: vw(2) 0;
    }
  }

  &__btn {
    color: var(--color-white);
    background-color: transparent;
  }
}
