.sauna {
  &__container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    // width: 76%;
    // padding: 0;mobile
  }

  &__caption {
    font-family: "Arkhip", san-serif;
    font-weight: 400;
    font-size: 55px;
    text-transform: uppercase;
    color: var(--greeny-blue);
    text-align: center;
    position: relative;
    margin-top: 159px;
    margin-bottom: 12px;

    @include mobile {
      margin-top: 79px;
    }

    &::after {
      z-index: 100;
      position: absolute;
      content: "";
      left: 152px;
      bottom: -17px;
      height: 100px;
      width: 240px;
      background-image: url(../img/wave.png);
      background-position: center;
      background-repeat: no-repeat;
      background-size: 46%;
      object-fit: cover;

      @include tablet {
        left: 133px;
        background-size: 23%;
      }

      @include small-tablet {
        left: 37px;
      }

      @include mobile {
        left: 48px;
        bottom: -31px;
      }
    }
  }

  &__text {
    margin: 17px auto;
    padding: 0px;
    font-family: Corbel;
    font-size: 21px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.36;
    letter-spacing: normal;
    text-align: center;
    color: var(--brownish-grey);
    width: 50%;

    @include mobile {
      width: 50%;
      font-size: 11px;
    }

    @include tablet {
      width: 96%;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-content: center;
    margin: 0;
    margin-top: 20px;
    margin-bottom: 35px;
  }

  &__items {
    padding: 40px 0px 50px 0px;
    width: 33.1%;
    display: block;
    font-family: Corbel;
    font-size: 31px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.13;
    letter-spacing: normal;
    text-align: center;
    color: var(--almost-black);

    &:nth-child(3n-1) {
      margin: 0px 0px;
    }

    @include mobile {
      font-size: 20px;
      padding: 0px 0px 16px 0px;
    }
  }
}

.sauna__svg {
  margin: 0px auto;
  margin-bottom: 20px;
  display: block;
  width: 91px;
  height: 91px;
}
