.footer {
  position: relative;
  z-index: 1;
  background-color: var(--light-grey);

  &__wrapper {
    position: relative;

  }

  &__container {
    margin-top: 15px;
    display: flex;
    justify-content: flex-start;

    @include tablet {
      flex-direction: column;
    }

    @include mobile {
      flex-direction: column-reverse;
    }
  }

  &__main-image {
    position: absolute;
    top: -90px;
    left: 1144px;

    @include big-desktop {
      top: -54px;
      left: auto;
      width: 35%;
      right: 0px;
    }
  }

  &__logo {
    width: 30%;
    position: relative;
    display: inline-block;
    background-color: var(--light-grey);

    @include mobile {
      width: 100%;
    }

    &::after {
      z-index: 100;
      position: absolute;
      content: "";
      left: 185px;
      top: 90px;
      height: 100px;
      width: 240px;
      background-image: url(../img/wave.png);
      background-position: center;
      background-repeat: no-repeat;
      background-size: 27%;
      object-fit: cover;

      @include mobile {
        top: 20px;
        left: 189px;
      }
    }

    &-text {
      letter-spacing: -2.1px;
      font-size: 71px;
      color: var(--greeny-blue);
      padding-top: 13px;
      padding-left: 4px;

      @include mobile {
        margin-top: 0;
      }
    }
  }

  &__svg {
    z-index: 100;
    position: absolute;
    right: 78px;
    top: 77px;
    height: 27px;
    width: 41px;
    object-fit: cover;
    transform: rotate(42deg);

    @include mobile {
      top: 12px;
      right: 20px;
    }
  }

  &__address {
    margin-left: 62px;
    padding-top: 80px;
    width: 30%;
    display: block;
    margin-bottom: 70px;

    @include big-desktop {
      padding-top: 0;
    }

    @include tablet {
      margin-left: 0;
      width: 100%;
      display: flex;
    }

    @include small-tablet {
      justify-content: space-around;
    }

    @include mobile {
      flex-direction: column-reverse;
      margin-bottom: 0;
    }
  }

  &__call {
    font-family: 'Corbel', sans-serif;
    font-size: 41px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 0.65;
    letter-spacing: 0.2px;
    text-align: left;
    color: var(--almost-black);
    margin: 0;

    @include small-tablet {
      font-size: 30px;
    }

    @include mobile {
      line-height: 32px;
      font-size: 22px;
    }
  }

  &__address-text {
    font-family: 'Corbel', sans-serif;
    font-size: 21px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.29;
    letter-spacing: normal;
    text-align: left;
    color: var(--charcoal-grey);
    margin-bottom: 33px;

    @include big-desktop {
      margin-bottom: 20px;
      padding-left: 20px;
    }

    @include tablet {
      padding-left: 0px;
      width: 30%;
      margin: 0;
    }

    @include mobile {
      width: 100%;
    }
  }
}

.social-list {
  display: block;
  width: 100%;

  @include big-desktop {
    @include flex-h-center;
  }

  @include tablet {
    width: 30%;
  }

  @include small-tablet {
    width: 15%;
  }

  @include small-tablet {
    flex-direction: column;
    align-items: center;
  }

  @include mobile {
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: row;
    width: 100%;
  }

  &__item {
    padding-right: 35px;
    padding-top: 46px;
    display: inline-block;

    @include tablet {
      padding-top: 0;
      padding-right: 18px;
    }
  }

  &__link {
    cursor: pointer;
  }

  &__svg {
    width: 49px;
    height: 49px;

    @include small-tablet {
      height: 69px;
    }
  }
}
