.extra {
  padding: 100px 0 0 0px;
  // background-image: url("../img/sauna.jpg");
  background-position: 0px 0px;
  background-repeat: no-repeat;
  background-size: cover;

  @include big-desktop {
    padding-top: 50px;
  }

  @include mobile {
    padding-top: 38px;
  }

  &__container {
    position: relative;
    flex-wrap: wrap;
    display: flex;
    // width: 76%;
    // padding: 0;
  }

  &__title {
    display: block;
    word-wrap: break-word;
    width: 17%;
    margin: 21px 217px 0px 0px;
    text-align: left;
    height: 300px;
    font-family: "Arkhip", san-serif;
    font-weight: 400;
    font-size: 55px;
    text-transform: uppercase;
    color: var(--greeny-blue);
    // text-align: center;
    // position: relative;
    // margin-top: 159px;
    // margin-bottom: 12px;

    @include big-desktop {
      text-align: center;
      margin: 0;
      height: auto;
      width: 100%;
    }

    @include mobile {
      width: 100%;
      font-size: 24px;
      margin-bottom: 10px;
      margin-right: 0px;
      height: auto;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    // justify-content: space-around;
    flex-wrap: wrap;
    width: 66%;
    max-height: 569px;
    padding-top: 15px;
    margin-bottom: 139px;


    @include big-desktop {
      width: 100%;
      padding-left: 55px;
    }

    @include mobile {
      // flex-direction: row;
      justify-content: space-around;
      // width: 80%;
      max-height: 250px;
      margin-bottom: 107px;
    }
  }

  &__items {
    position: relative;
    display: block;
    padding: 0px 78px 72px 0px;
    width: 52%;
    margin: 0;
    margin-right: 61px;
    text-transform: uppercase;
    font-family: 'Corbel', sans-serif;
    font-size: 31px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.14;
    letter-spacing: normal;
    text-align: left;
    color: var(--color-white);

    @include tablet {
      font-size: 25px;
    }

    @include mobile {
      width: 47%;
      font-size: 12px;
      padding: 0 22px 22px 0px;
      margin-right: 0px;
    }

    &::after {
      content: '';
      position: absolute;
      left: -56px;
      top: -5px;
      height: 44px;
      width: 41px;
      background-image: url(../img/svg/leaf.svg);
      background-position: center;
      background-repeat: no-repeat;
      background-size: 102%;
      object-fit: cover;

      @include mobile {
        top: -17px;
        width: 22px;
        left: -32px;
      }


      &:nth-child(2) {
        // margin-left: 12px;
        order: 7;
        width: 85%;
      }

      &:nth-child(7) {
        order: 2;
      }
    }

    &:nth-child(4n) {
      white-space: nowrap;
    }

    &:nth-child(6n) {
      white-space: nowrap;
    }
  }



  &__svg {
    display: inline-block;
    z-index: 100;
    height: 41px;
    width: 44px;
    object-fit: cover;

    @include mobile {
      height: 31px;
      width: 34px;
    }
  }

  &__flower {
    position: absolute;
    bottom: 0;

    &_midi {
      height: 24.2%;
      left: 99px;


    }

    &_small {
      height: 27.5%;
      left: 9px;

      @include big-desktop {
        left: 139px;
      }
    }

    &_bigi {
      height: 38%;
      left: 58px;

      @include big-desktop {
        left: 458px;
      }

      @include small-tablet {
        left: 240px;
      }
    }
  }

}
